.report {
    text-align: center;

    &__list {
        display: flex;
        justify-content: center;
    }   

    &__item {
        width: 25%;
        text-align: center;

        &:not(:first-child) .report__card {
            border-left: 1px #fff solid;
        }
    }

    &__card {
        display: block;
        width: 100%;
        padding-inline: auto;
    }

    &__title {
        margin-bottom: 22px;
    }
}